import React from "react";
import "hui/step-flow/flow-step";

export default class HAFlowStep extends React.Component {
    static propTypes = {
        className: React.PropTypes.string,
        id: React.PropTypes.string,
        stepId: React.PropTypes.string,
        showSaveForLaterButton: React.PropTypes.bool,
        saveForLaterButtonText: React.PropTypes.string,
        nextButtonText: React.PropTypes.string,
        previousButtonText: React.PropTypes.string,
        titleText: React.PropTypes.string,
        subtitleText: React.PropTypes.string,
        progressIndicatorText: React.PropTypes.string,
        stepComplete: React.PropTypes.bool,
        stepAvailable: React.PropTypes.bool,
        hideProgressIndicator: React.PropTypes.bool,
        hideProgressIndicatorItem: React.PropTypes.bool,
        hideStepButtons: React.PropTypes.bool,
        useCustomButtons: React.PropTypes.bool,
        content: React.PropTypes.object,
        validator: React.PropTypes.func,
        beforeShowPromise: React.PropTypes.func
    };

    constructor(props) {
        super(props);
        this._huiComponent = null;
    }

    componentDidMount() {
        this.setHuiProperties();
        this.updateSection();
    }

    componentDidUpdate() {
        this.setHuiProperties();
    }

    // insert React render components into web component
    updateSection() {
        let flowSection;
        if (this._huiComponent) {
            flowSection = this._huiComponent.querySelectorAll('section');
        }
        if (flowSection && flowSection.length > 1) {
            let children = Array.prototype.slice.call(flowSection[1].childNodes);
            children.forEach(child => {
                flowSection[0].appendChild(child);
            });
            this._huiComponent.removeChild(flowSection[1]);
        }
    }

    setHuiProperties() {
        if (this.props.validator) {
            this._huiComponent.validator = this.props.validator;
        }
        if (this.props.beforeShowPromise) {
            this._huiComponent.beforeShowPromise = this.props.beforeShowPromise;
        }
    }

    handleRef = (c) => {
        this._huiComponent = c;
    };

    render() {
        /* jshint ignore:start */
        return (
            <ha-flow-step
                ref={this.handleRef}
                class={this.props.className}
                {...this.props}>
                {this.props.children}
            </ha-flow-step>
        );
        /* jshint ignore:end */
    }
}
